    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .auth_image_content {
    width: 350px;
    height: 100px;
    line-height: 100px;

    display: flex;
    justify-content: space-between;
  }
  .logo_spotify {
    margin-left: 50px;
    height: 100px;
