@media only screen and (max-width: 603px) {
  .newPlaylistPage {
    width: 100%;
    height: 100%;
  }
  .searchForMyPlaylist {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
    margin: auto;
  }
  .spaceItemAndInput {
    min-width: 190px;
    display: flex;
    justify-content: space-evenly;
  }
  .spaceItemAndInput_Item {
    font-weight: 600;
    margin: 0 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
  input {
    max-width: 110px !important;
  }
  input[type="submit"] {
    border: 0;
    line-height: 30px;
    padding: 1px 3px;
    text-align: center;
    color: #fff;
    text-shadow: 1px 1px 1px #000;
    border-radius: 10px;
    background-color: rgb(59, 0, 220);
    background-image: linear-gradient(
      to top left,
      rgba(0, 0, 0, 0.2),
      rgba(0, 0, 0, 0.2) 30%,
      rgba(0, 0, 0, 0)
    );
    box-shadow: inset 2px 2px 3px rgba(255, 255, 255, 0.6),
      inset -2px -2px 3px rgba(0, 0, 0, 0.6);
  }
  .tracksList {
    width: 100%;
    height: 400px;
    background: linear-gradient(#e66465, #9198e5);
    margin: 20px 5px 5px 5px;
    display: flex;
    flex-wrap: wrap;
    overflow: auto;
  }
  .tracks {
    width: 46%;
    margin: 0 2%;
  }
}
